	module.exports = {"data":{"post":{"html":"<p>Thanks to <a href=\"http://burningbird.net/cephalopods/three-for-the-cephalopods/\">Shelley for pointing</a> to this <a href=\"http://cephalopodcast.com/blog/2007/03/28/weird-aquaria-fast-food-fryer-live-goldfish-tank/\">combination goldfish tank and fast food fryer</a>. But I doubt that our field consultant would approve it.</p>\n<blockquote><p>\n  Because oil floats on water, despite the massive heat (163 degrees Celsius) the goldfish simply stay away from the surface and all is well. They eat the crumbs of croquettes and other fried foods that fall to the bottom, and can live in there for 5-10 years as they happily clean away, ignorant to the fact that certain death awaits any potential escapees.\n</p></blockquote>\n<p><object width=\"425\" height=\"350\"><param name=\"movie\" value=\"http://www.youtube.com/v/ZH96MhITOlk\" /><embed src=\"http://www.youtube.com/v/ZH96MhITOlk\" type=\"application/x-shockwave-flash\" width=\"425\" height=\"350\"></embed></object></p>","fields":{"title":"Fried Fish","date":"29 Mar 2007","url":"https://papascott.de/2007-03-29-fried-fish/"}}},"pathContext":{"slug":"/2007-03-29-fried-fish/","prev":{"fields":{"title":"Hamburg Sunny Day","date":"28 Mar 2007","slug":"/2007-03-28-hamburg-sunny-day/"}},"next":{"fields":{"title":"All McDonald's in Germany Smoke-Free","date":"30 Mar 2007","slug":"/2007-03-30-all-mcdonalds-in-germany-smoke-free/"}}}}

